import React , {Component} from 'react';
import styled from '@react-pdf/styled-components';
import { Page, Text, View, Document, StyleSheet } from '@react-pdf/renderer';


// Create styles
const styles = StyleSheet.create({
  page: {
    flexDirection: 'row',
    backgroundColor: '#E4E4E4'
  },
  section: {
    margin: 10,
    padding: 10,
    flexGrow: 1
  },
  resultActivity: {
    flexDirection: 'row',
    backgroundColor: 'white',
  },
  day: {
    backgroundColor: 'blue'
  }
});

const DayBox = styled.Text`
  background-color: blue
`
class PDFRender extends Component {
  render() {
    return <Document>
      <Page size="A4" style={styles.page}>
        <View style={styles.section}>
          {
            this.props.trip.itinerary.map((dayValue, dayIndex) => {
              return(
              <View style={styles.day}>

                  <Text> Day {dayIndex} </Text>
                  {
                    dayValue.map((value, index) => {
                      if (index !== 0 && index !== (dayValue.length - 1)){
                      return (
                        <View style={styles.resultActivity}>
                          <View>
                            <Text> TIME </Text>
                          </View>

                          <View>
                            <Text> PIC </Text>
                          </View>

                          <View>
                            <Text> {value.name} </Text>
                          </View>

                        </View>)
                      }
                    })
                  }

              </View>)
            })
          }
        </View>
        <View style={styles.section}>
          <Text>Section #2</Text>
        </View>
      </Page>
    </Document>
  }

}

export default PDFRender;
